@tailwind base;
@tailwind components;
@tailwind utilities;

.slideshow {
    margin: 0 auto;
    overflow: hidden;
  }
  
  .slideshowSlider {
    white-space: nowrap;
    transition: ease 1000ms;

  }
  
  .slide {
    display: inline-block;
    object-fit: cover;
  
    height: 760px;
    width: 100%;
  }
  
  .slideshowDots {
    text-align: center;
  }
  
  .slideshowDot {
    display: inline-block;
    height: 20px;
    width: 20px;
    border-radius: 50%;
  
    cursor: pointer;
    margin: 15px 7px 0px;
  
    background-color: #c4c4c4;
  }
  
  .slideshowDot.active {
    background-color: #6a0dad;
  }
  